<template>
    <div class="modal-layout">
    <div class="darken" @click="closeModal()">
        <div class="modal-window" :class="(type == 'welcomeBonus' ? 'welcome-bonus' : (type == 'socialBoom' ? 'social-boom' :(type == 'operation' ? 'operation' : ''))) + (step > 1 ? 'animated' : '')" @click.stop="">
        <div class="modal-back-button-container" :class="type=='welcomeBonus' ? 'welcome-bonus': ''">
            <button class="modal-back-button" @click="closeModal"></button>
        </div>
        
        <div class="modal-container">
            <div class="modal-content">
                <p class="window-title">{{$localizeFilter('social_boom_participant_application')}}</p>

                <div class="user-info">
                    <div class="info-item">
                        <div class="field-name">{{$localizeFilter('social_boom_name_on_website')}}</div>
                        <div class="field-value">{{ name }}</div>
                    </div>
                    <div class="info-item">
                        <div class="field-name">{{$localizeFilter('social_boom_telegram')}}</div>
                        <div class="field-value">{{ telegram }}</div>
                    </div>
                    <div class="info-item">
                        <div class="field-name">{{$localizeFilter('social_boom_email')}}</div>
                        <div class="field-value">{{ email }}</div>
                    </div>
                </div>

                <p class="window-text">{{$localizeFilter('social_boom_write_links_to_your_posts')}}</p>

                <div class="links">
                    <div class="link" v-for="link, idx in links" :key="idx">
                        <input class="input-field" type="text" v-model="link.text" :placeholder="$localizeFilter('social_boom_link') + (idx+1)"><div class="button-link-wrapper"><button class="basic-button add-link-button" v-if="idx == links.length - 1 && links.length < 4" @click="addLink">{{$localizeFilter('social_boom_add_link')}}</button></div>
                    </div>
                </div>

                <div class="coin-block">
                    <div class="header">
                        <img src="@/assets/newImages/coins/icons/gdc_icon.png" alt="">
                        <div class="coin">GadgetCoin</div>
                    </div>
                    <div class="content">{{$localizeFilter('social_boom_will_go_to_bonus_account')}}</div>
                </div>
                <div class="coin-block last">
                    <div class="header">
                        <img src="@/assets/newImages/coins/icons/dom_icon.png" alt="">
                        <div class="coin">DOM</div>
                    </div>
                    <div class="content">{{$localizeFilter('social_boom_write_metamask_wallet')}}</div>
                </div>


                <input id="address" class="custom-input wallet" :placeholder="$localizeFilter('wallet_number', ['Ethereum'])" type="text" v-model.trim="address" :class="{ invalid: v$.address.$error }"  v-maska="{tokens: { 'H': { pattern: /[0-9a-fA-F]/, uppercase: true }}, mask: '0xHHHHHHHHHHHHHHHHHHHHHHHHHHHHHHHHHHHHHHHH'}"/>

                <!-- <div class="links">
                    <div class="link">
                        <input type="text" class="input-field" v-model="address" placeholder="Номер кошелька Ehtereum"><div class="button-link-wrapper"></div>
                    </div>
                </div> -->
                

                <div class="conditions-block" @click="acceptСonditions1">
                    <input type="checkbox" id="checkbox" class="checkbox" v-model="conditions1Accepted">
                    <div class="checkbox-labels-block">
                        <label for="checkbox" class="conditions-label" v-html="$localizeFilter('social_boom_accept_conditions_1')" @click="acceptСonditions1"></label>
                    </div>
                </div>


                <div class="conditions-block" @click="acceptСonditions2">
                    <input type="checkbox" id="checkbox2" class="checkbox" v-model="conditions2Accepted">
                    <div class="checkbox-labels-block">
                        <label for="checkbox2" class="conditions-label" v-html="$localizeFilter('social_boom_accept_conditions_2')" @click="acceptСonditions2"></label>
                    </div>
                </div>

                <button class="basic-button submit" :class="(conditions1Accepted && conditions2Accepted) ? '' : 'inactive'" @click="submitHandler">{{$localizeFilter('send')}}</button>
            </div>
        </div>
        </div>
        </div>
    </div>
</template>

<script>
import { defineComponent } from '@vue/composition-api'
import useVuelidate from "@vuelidate/core";
import { required, numeric, minValue, minLength, maxLength } from "@vuelidate/validators";
import localizeFilter from '@/filters/localize.filter'

export default defineComponent({
    data: () => ({
        address: "",
        links: [{}],
        conditions1Accepted: false,
        conditions2Accepted: false
    }),
     setup() {
        return { v$: useVuelidate() };
    },
    validations() {
        return {
            address: { required, minLength: minLength(42), maxLength: maxLength(42) }
        };
  },
  computed: {
      name(){
          return this.$store.state.info?.profile?.firstName + ' ' + this.$store.state.info?.profile?.lastName
      },
      telegram(){
          return '@' + this.$store.state.info?.profile?.telegramName
      },
      email(){
          return this.$store.state.info?.profile?.email
      }
  },
  methods: {
      addLink(){
          this.links.push({})
      },
    async submitHandler() {
        this.v$.$touch();
        if (this.v$.$error || !this.conditions1Accepted || !this.conditions2Accepted) {
            return;
        }
         try {
            const formData = {offerName: 'socialBoom', data: {links: this.links.map(link=>link.text), DOMWithdrawAddress: this.address}};
            await this.$store.dispatch('joinSpecialOffer', formData)
            this.$message(localizeFilter('social_boom_send_successful'))
            this.$emit('closeModal', false)
        } catch(e) {
            if (e.message == 'Not enough funds') {
                // this.notEnoughFunds = true
            } else {
                // this.notEnoughFunds = false
                this.$error(e)
            }
        } 
      },
    acceptСonditions1() {
        this.conditions1Accepted = !this.conditions1Accepted
    },
    acceptСonditions2() {
        this.conditions2Accepted = !this.conditions2Accepted
    },
  },
     async mounted() {
        try {
            await this.$store.dispatch('getProfile')
            this.$forceUpdate()
        } catch(e) {
            this.$error(e)
        } 
    }
})
</script>

